import React from 'react';
import PropTypes from 'prop-types';
import PageWrapper from 'components/templates/PageWrapper/PageWrapper';
import {
  StyledForm,
  LogoWrapper,
  StyledButton,
  StyledSpan,
  ErrorMessage,
} from './Login.style';
import Input from 'components/atoms/Input/Input';
import { ReactComponent as Logo } from 'assets/icons/logo.svg';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { signin } from 'actions/authActions';
import { SIGNUP } from 'constants/routes';
import { Link } from 'react-router-dom';
import Loader from 'components/atoms/Loader/Loader';

const Login = (props) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const dispatch = useDispatch();
  const { apiCallProgress } = useSelector((state) => state.apiCallsReducer);
  const goHome = () => props.history.push('/');
  const onSubmit = (data) => {
    dispatch(signin(data.email, data.password, goHome));
  };

  return (
    <PageWrapper>
      <LogoWrapper>
        <Logo />
      </LogoWrapper>
      <StyledForm onSubmit={handleSubmit(onSubmit)}>
        <Input
          placeholder="Email"
          type="email"
          {...register('email', { required: true })}
        />
        {errors.email && (
          <ErrorMessage role="alert">Podaj adres email!</ErrorMessage>
        )}
        <Input
          placeholder="Hasło"
          type="password"
          {...register('password', { required: true })}
        />
        {errors.password && (
          <ErrorMessage role="alert">Podaj hasło!</ErrorMessage>
        )}
        <StyledButton disabled={apiCallProgress === 1} type="submit">
          ZALOGUJ SIĘ
        </StyledButton>
      </StyledForm>

      <StyledSpan>Nie masz jeszcze konta?</StyledSpan>
      <Link to={SIGNUP}>
        <StyledSpan isColor={true}>Zarejestruj się!</StyledSpan>
      </Link>
      {apiCallProgress === 1 ? <Loader /> : null}
    </PageWrapper>
  );
};

Login.propTypes = {};

export default Login;
